.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
  text-align: center;
  background: #fff;
  padding: 20px;
  font-family: sans-serif;
}

#submitterbit {
  position: fixed;
  background-color: #fff;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  margin: 0;
  padding: 5px;
  border-top: solid 2px #444;
}


input {
  width: 280px;
}

input:read-only {
  border: 0;
  color: #545458;
  font-weight: bold;
  text-align: center;
}

button {
  width: 100%;
  max-width: 300px;
  appearance: button;
  padding: 13px 16px;
  background-color: #AEAEAE;
  box-shadow: inset 0 -4px 0 #110221;
  border-radius: 10px;
  color: #FFFFFF;
  cursor: pointer;
  display: inline-block;
  font-size: 15px;
  font-weight: 700;
  letter-spacing: 3px;
  line-height: 20px;
  margin: 2px;
  border: solid transparent;
  border-width: 0;
  text-transform: uppercase;
  touch-action: manipulation;
   transition-duration: 0.05s;
  transition-timing-function: linear;
  transition-delay: 0;
  user-select: none;
  -webkit-user-select: none;
}

.care {
  background-color: #d03fc2;
}

.timemoney {
  background-color: #F4C630;
}

.health {
  background-color: #4783F5;
}

button.active {
  padding-top: 17px;
  padding-bottom: 9px;
  box-shadow: inset 0 4px 0 #fff;
}


button:hover:not(:disabled) {
  filter: brightness(1.1);
  -webkit-filter: brightness(1.1);
}

#submitbutton {
  background-color: #444448;
  color: #f8f8ff;
}
#successbutton {
  background-color: #30f550;
  color: forestgreen;
}

.gyear {
  display: inline-block;
  cursor: default;
  user-select: none;
  font-size: small;
}

.gweek {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
  padding: 0;
  height: 25px;
  color: #444;
  font-size: x-small;
}

.gweekLeftgutter, .gweekRightgutter {
  display: inline-block;
  padding-left: 5px;
  padding-right: 5px;
  display: inline-block;
  width: 80px;
  text-align: right;
  vertical-align: middle;
}

.gday {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
  gap: 0;
  row-gap: 0;
  column-gap: 0;
  width: 24px;
  height: 24px;
  border: 1px solid #ccd4e2;
  border-right: 0;
  border-bottom: 0;
  font-size: xx-small;
  color: #aaaab4;
  vertical-align: middle;
  background-color: #FFFFFF;
}

.gday div {
  line-height: 2px;
}

.gday.past {
  color: #ddd;
}

.gday:nth-last-child(2) {
  border-right: 1px solid #ccd4e2;
}

.gweek:last-child > .gday {
  border-bottom: 1px solid #ccd4e2;
}

.gday:nth-last-child(2), .gday:nth-last-child(3) {
  background-color: #f8f8f9;
}

.gweek:hover {
  filter: brightness(0.85);
  -webkit-filter: brightness(0.85);
}

.gweek:hover > .gday.today, .gday.today {
  background-color: #444448;
  color: #fff;
}

.gweek:hover > .gday:hover {
  color: #000000;
  background-color: #a9a9a9;
}

.gpip {
  background-color: #545458;
  width: 26px;
  height: 2px;
}


.gdaybyhours {
  display: inline-block;
  width: 55px;
  position: relative;
  user-select: none;
}

.gdayhour {
  height: 24px;
  border-left: 1px solid #ccd4e2;
  border-right: 1px solid #ccd4e2;
  border-top: 1px dashed #ccd4e2;
  border-bottom: 0;
}
.gdayhour:nth-child(odd) {
  background-color: #fafafb;
}
.gdayhour:nth-child(even) {
  background-color: #fafafb;
}
.gdayhour:last-child {
  border-bottom: 1px solid #ccd4e2;
}
.gdayhour:first-child {
  border-top: 1px solid #ccd4e2;
}

.gevent {
  position: absolute;
  left: -1px;
  background-color: #4783F5;
  border: 1px solid rgba(0,0,0,0.25);
  width: 100%;
  font-size: xx-small;
  font-weight: bold;
  color: #fff;
  text-transform: uppercase;
}

.gevent:hover {
  filter: brightness(1.1);
  -webkit-filter: brightness(1.1);
}

.gpie {
  display: inline-block;
  width: 67%;
  max-width: 300px;
  aspect-ratio: 1 / 1;
  border-radius: 50%;
  box-shadow: inset 0 -4px 0 #110221;
  place-content: center;
  color: #FFFFFF;
  font-size: 15px;
  font-weight: 700;
  letter-spacing: 3px;
  text-transform: uppercase;
  user-select: none;
  -webkit-user-select: none;
}


.gclock {
  cursor: pointer;
  position: relative;
  margin: auto;
  width: 67%;
  max-width: 300px;
  aspect-ratio: 1 / 1;
  background-color: #AEAEAE;
  border-radius: 50%;
  border: 8px solid #a9a9a9; 
  box-shadow: inset 0 -4px 0 #110221;
  background-image:
    radial-gradient(circle at center, #fff 8px, transparent 8px),
    radial-gradient(circle at center, #AEAEAE 50%, transparent 50%, transparent 65%, #AEAEAE 65%),
    linear-gradient(to right, transparent 49.5%, #fff 49.5%, #fff 50.5%, transparent 50.5%),
    linear-gradient(to bottom, transparent 49.5%, #fff 49.5%, #fff 50.5%, transparent 50.5%),
    linear-gradient(-30deg, transparent 49.75%, #fff 49.75%, #fff 50.25%, transparent 50.25%),
    linear-gradient( 30deg, transparent 49.75%, #fff 49.75%, #fff 50.25%, transparent 50.25%),
    linear-gradient( 60deg, transparent 49.75%, #fff 49.75%, #fff 50.25%, transparent 50.25%),
    linear-gradient(-60deg, transparent 49.75%, #fff 49.75%, #fff 50.25%, transparent 50.25%);
}

.hour-hand {
  position: absolute;
  height: 25%;
  width: 3%;
  margin: auto;
  top: -24.5%;
  left: 0;
  bottom: 0;
  right: 0;
  border-radius: 4px;
  background-color: #fff;
  transform-origin: bottom;
  transform: rotate(0deg);
}

.minute-hand {
  position: absolute;
  height: 39%;
  width: 3%;
  margin: auto;
  top: -38.5%;
  left: 0;
  bottom: 0;
  right: 0;
  border-radius: 4px;
  background-color: #fff;
  transform-origin: bottom;
  transform: rotate(90deg);
}

.second-hand {
  position: absolute;
  height: 39%;
  width: 1%;
  margin: auto;
  top: -38.5%;
  left: 0;
  bottom: 0;
  right: 0;
  border-radius: 4px;
  background-color: #fff;
  transform-origin: bottom;
  transform: rotate(180deg);
}

